












































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Buttonmenu from "@/components/shared/Buttonmenu.vue";
import Tutorial from "@/components/shared/Tutorial.vue";
// import TutorialAnimation from "@/components/shared/TutorialAnimation.vue";
import Modal from "@/components/shared/Modal.vue";
import WeightsPath from "@/components/paths/Weights.vue";
import SimplePath from "@/components/paths/Simple.vue";
import MultiPath from "@/components/paths/Multi.vue";
import Roundtrip from "@/components/paths/Round.vue";



export enum GameType {
  WEIGHTSPATH = "WeightsPath",
  SIMPLEPATH = "SimplePath",
  MULTIPATH = "MultiPath",
  ROUNDTRIP = "Roundtrip",
}

@Component({
  components: {
    Buttonmenu,
    // Tutorial,
    // TutorialAnimation,
    Modal,
    WeightsPath,
    MultiPath,
    SimplePath,
    Roundtrip,
  },
})
export default class Game extends Vue {
  @Prop({ required: true })
  type: GameType;
  @Prop({ type: Object, default: () => ({}) })
  args: {};

  resultText = "";
  isCorrect = [false, true];
  showResult = false;
  showAnimation = false;

  evaluatedGame(correct: boolean[]): void {
    if (correct[1]){
      if (correct[0]) {
        this.resultText = "Richtig!";
        this.isCorrect[0] = true;
      } else {
        this.resultText = "Falsch!";
        this.isCorrect[0] = false;
      }
      this.showResult = true;
      setTimeout(() => {
        this.showResult = false;
      }, 1500);
    }
  }

  get currentGameComponent() {
    return this.type;
  }
}
